	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"https://facebook.github.io/react-native/\">React-native</a> boosts an impressive list of features. Importantly it allows\nfor developers to create native mobile applications using JavaScript and <a href=\"https://facebook.github.io/react/\">React.js</a>.</p>\n<p>This presentation highlights the developer friendly ecosystem, and powerful developer workflow that can be applied.</p>\n<p>Including, but not limited to:</p>\n<ul>\n<li>Declarative Component / View creation using <a href=\"https://facebook.github.io/react/\">React</a></li>\n<li>Debugging via Chrome's developer tools</li>\n<li><a href=\"https://facebook.github.io/react-native/blog/2016/03/24/introducing-hot-reloading.html\">Hot module reloading</a></li>\n<li>Time traveling and state management with <a href=\"http://redux.js.org/\">Redux</a></li>\n</ul>","frontmatter":{"title":"React-native Presentation","date":"26 April, 2017"},"fields":{"slug":"/talks/2017-04-26-react-native-presentation/"}}},"pathContext":{"sidebarPath":null,"slug":"/talks/2017-04-26-react-native-presentation/"}}

